.main-block {
  background-image: linear-gradient(to top, #fff 179px, transparent 179px), url(../img/main-block/cat-main-block-background.webp);
  background-color: @main-block-green;
  background-repeat: no-repeat;
  background-position-x: center;
  margin-bottom: 21px;
  @media (max-width: @only-mobile-width) {
    min-height: 481px;
  }
  @media (min-width: @tablet-width) {
    background: none;
    margin-bottom: 55px;
  }
  @media (min-width: @desktop-width) {
    min-height: 608px;
    &::before {
      content: "";
      display: block;
      position: absolute;
      width: 50%;
      height: 694px;
      top: 0;
      right: 0;
      background-image: url(../img/main-block/cat-main-block-desktop.webp);
      background-color: #65a93c;
      background-repeat: no-repeat;
      background-position: right;
      z-index: -2;
    }
  }
}

.main-block__wrapper {
  position: relative;
  margin: 0 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-top: 27px;
  &::after {
    content: "";
    position: absolute;
    top: 167px;
    left: 50%;
    transform: translateX(-50%);
    width: 280px;
    height: 270px;
    background-image: url(../img/main-block/index-can-mobile1@2x.webp);
    background-image: image-set(url(../img/main-block/index-can.webp) 1x, url(../img/main-block/index-can-mobile1@2x.webp) 2x);
    background-size: cover;
    z-index: 1;
  }
  @media (min-width: @tablet-width) {
    margin: 0 30px;
    padding-top: 45px;
    min-height: 800px;
    display: block;
    &::after {
      content: "";
      position: absolute;
      top: 247px;
      width: 709px;
      height: 609px;
      background-image: url(../img/main-block/index-can-tablet@2x.webp);
      background-image: image-set(url(../img/main-block/index-can-tablet.webp) 1x, url(../img/main-block/index-can-tablet@2x.webp) 2x);
      z-index: -1;
    }
  }
  @media (min-width: @desktop-width) {
    margin: 0 110px;
    padding-right: 50%;
    padding-top: 112px;
    min-height: 0;
    &::after {
      width: 552px;
      height: 499px;
      background-image: url("../img/main-block/index-can-desctop@2x.webp");
      background-image:
        image-set(
          url("../img/main-block/index-can-desctop.webp") 1x,
          url("../img/main-block/index-can-desctop@2x.webp") 2x
          );
      top: 28px;
      left: 37%;
      transform: none;
    }
  }
}

.main-block__title {
  line-height: 36px;
  text-align: center;
  color: @white;
  text-align: center;
  margin-bottom: 25px;
  @media (min-width: @tablet-width) {
    font-size: 60px;
    line-height: 60px;
    color: @black;
    margin-left: 87px;
    margin-right: 87px;
    text-align: left;
    margin-bottom: 42px;
  }
  @media (min-width: @desktop-width) {
    margin-left: 80px;
    margin-right: 0;
    margin-bottom: 33px;
  }
}

.main-block__subtitle {
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  color: @white;
  text-align: center;
  margin: 0;
  margin-bottom: 30px;
  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 20px;
    color: @black;
    text-align: left;
    margin-left: 87px;
    margin-bottom: 48px;
  }
  @media (min-width: @desktop-width) {
    font-size: 20px;
    line-height: 30px;
    margin-left: 80px;
  }
}

.main-block__img {
  display: block;
  width: 280px;
  height: 270px;
  object-fit: contain;
  margin: 0 auto;
  @media (min-width: @tablet-width) {
    display: none;
  }
}

.main-block__img-tablet {
  display: none;
  @media (min-width: @tablet-width) {
    margin: 0 auto;
    display: block;
  }
  @media (min-width: @desktop-width) {
    display: none;
  }
}

.main-block__img-desctop {
  display: none;
  @media (min-width: @desktop-width) {
    display: block;
    position: absolute;
    right: 28%;
    top: 20%;
    z-index: -1;
  }
}

.main-block .main-block__button {
  margin-top: 3px;
  padding-top: 11px;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  @media (max-width: @only-mobile-width) {
    margin-top: 272px;  }
  @media (min-width: @tablet-width) {
    width: 244px;
    margin-left: 82px;
    font-size: 20px;
    line-height: 20px;
    padding: 17px 15px;
  }
  @media (min-width: @desktop-width) {
    margin-left: 80px;
  }
}
